/**
 * LobsterBoard - Dashboard Builder & Widget Library
 * Main entry point for npm package
 */

// Import CSS (handled by rollup-plugin-postcss)
import '../css/builder.css';

// Import core modules
import * as builder from './builder.js';
import * as widgets from './widgets.js';

// Named exports for ES module users
export { builder, widgets };

// Default export with everything
export default {
  builder,
  widgets,
  version: '0.1.0'
};

// UMD global exposure (for script tag users)
if (typeof window !== 'undefined') {
  window.LobsterBoard = {
    builder,
    widgets,
    version: '0.1.0'
  };
}
